<template>
  <div class="index-box">
    <div class="header">
      <div class="box1">
        <div class="logo"></div>
      </div>
    </div>
    <div class="content">
      <p>{{p}}</p>
      <pre><span class="sign">JavaScript</span><code data-language="JavaScript">{{fun}}</code></pre>
      <pre><span class="sign">CSS</span><code data-language="css">{{css}}</code></pre>
      <pre><span class="sign">HTML</span><code data-language="html">{{html}}</code></pre>
    </div>
  </div>
</template>

<script>
  import data from '../data.json';
  export default {
    name: 'app',
    data () {
      return {
        fun: `function Stack() {
  this.length = function () {
    return this.top;
  };
  this.top = 0;
  this.stackStore = [];
  this.push = function (str) {
    this.stackStore[this.top++] = str;
  };
}`,
        css: `pre {
  width: 100%;
  padding: 0 10px;
  background: #272822;
  box-sizing: border-box;
  color: #fff;
  border-radius: 5px;
  line-height: 16px;
  margin-bottom: 0;
  font-size: 0;
}`,
        html: `<div class='header'>
  <div class='box1'>
    <div class='logo'></div>
  </div>
</div>`,
        p: `我们已经生成了我们的第一个 Vue 应用！看起来这跟单单渲染一个字符串模板非常类似，但是 Vue.js 在背后做了大量工作。现在数据和 DOM
        已经被绑定在一起，所有的元素都是响应式的。我们如何知道？打开你的浏览器的控制台，并修改 app.message，你将看到上例相应地更新。`
      }
    },
    methods: {}
  };

</script>

<style lang="less" scoped>
  @import '../lib/swiper/swiper.css';
  @import '../lib/reset.css';
  @import '../lib/monokai.css';

  .index-box {
    background: #fff;
    width: 100%;
    height: 100%;
    .content {
      width: 100%;
      max-width: 1000px;
      margin: 0 auto;
      padding: 10px;
      box-sizing: border-box;
      font-size: 15px;
      color: #333;
      line-height: 25px;
      p {
        text-indent: 20px;
        margin: 5px 0;
      }
      pre {
        width: 100%;
        padding: 10px;
        background: #272822;
        box-sizing: border-box;
        color: #fff;
        border-radius: 5px;
        line-height: 16px;
        margin: 10px 0;
        font-size: 0;
        position: relative;
        .sign {
          position: absolute;
          right: 10px;
          top: 10px;
          color: #888;
          font-size: 12px;
        }
        code {
          font-size: 12px;
        }
      }
    }
    .header {
      width: 100%;
      height: 51px;
      color: #333;
      background: #fff;
      .box1 {
        background: #fff;
        width: 100%;
        /*max-width: 500px;*/
        margin: 0 auto;
        height: 50px;
        border-bottom: 1px solid #eee;
        position: fixed;
        z-index: 1000;
        .logo {
          width: 100%;
          max-width: 500px;
          height: 100%;
          margin: 0 auto;
          background: url("https://raw.githubusercontent.com/beautifulBoys/beautifulBoys.github.io/master/source/home/blog.jpg") no-repeat center center;
          background-size: 40%;
        }
      }
    }
  }


</style>



